.App {
  text-align: center;
  color: white;
}

.App-logo {
  animation: App-logo-spin infinite 40s linear;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 40s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: #444b5a;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

p{
  font-weight: bold;
}
img{
  image-rendering: pixelated;
  width: 128px;
}
.shadowed{
  color: rgb(63, 63, 63);
  text-shadow:
  -1px 1px 0 #000;
}
.smallfont{
  font-size: small;
}

nav{
  height: 60px;
  background-color: #62d9fb;
  font-size: 20px;
}
.container{
  background-color: rgb(38, 46, 68);
  margin-left: 150px;
  margin-right: 150px;

}